import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Treinamento_funcao } from '../../../modelo/Treinamento_funcao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'treinamento_funcao-dados-edit',   templateUrl: 'treinamento_funcao_dadosEdit.component.html' }) export class Treinamento_funcaoDadosEditComponent implements OnInit {    treinamento_funcao: Treinamento_funcao;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.treinamento_funcao.Treinamento_funcao";                 let id = +params['id'];         this.treinamento_funcao = new Treinamento_funcao();         this.treinamento_funcao.cd_treinamento_funcao = id;         o.objetoJson = this.treinamento_funcao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a treinamento_funcao. ';       }      this.treinamento_funcao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


